/************************************/
/************** Root ************/
/************************************/
:root {
  --primary-white: #f8f8f8;
  --primary-accent: #0077cc;
  --primary-darks: #333333;
  --button--color-1: #e0c351;
  --button--color-2: #000;
  --button--ornament-offset: -0.5em;
}


/************************************/
/************** Body ************/
/************************************/
html {
  overflow-x: hidden;
}

body {
  font-family: 'Poppins', sans-serif;
  margin: 0 auto;
  overflow-x: hidden;
  width: auto;
}

/************************************/
/************** NavBar ************/
/************************************/
.Logo {
  margin-left: 6rem;
  fill: white;
  padding: 0.5rem 0.75rem;
  transition: fill 0.2s ease-in-out;
}

.Logo:hover {
  fill: #0077cc;
  fill: var(--primary-accent);
}


.logo {
  max-height: 2.5rem;
}





.navbar-dark {
  background-color: #333333 !important;
  background-color: var(--primary-darks) !important;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .75);
}

.navbar-toggler {
  border: none !important;
  color: #0077cc !important;
  color: var(--primary-accent) !important;
}

.navbar-nav {
  margin-right: 6rem;
}



nav a.nav-link {
  font-weight: 400;
  letter-spacing: 0.1rem;
  margin-top: 0.5rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
  text-align: center;
  /* font-family: 'Roboto Slab', serif; */
}

.navbar-nav>li>a {
  margin-right: 1rem;
  margin-left: 1rem;
  width: 6.5rem;
  color: #f8f8f8 !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid #f8f8f8;
  border-top: 0.1875rem solid var(--primary-white);
}

.navbar-nav>li>a:hover {
  border-top: 0.1875rem solid #0077cc;
  border-top: 0.1875rem solid var(--primary-accent);
}

.navbar-nav>li>a.active,
.navbar-nav>li>a.active:focus {
  border-top: 0.1875rem solid #0077cc;
  border-top: 0.1875rem solid var(--primary-accent);
}

@media(max-width: 991px) {
  .header-message-container {
    display: block;
    margin: 0 auto;
  }

  .Logo {
    margin-left: 1rem;
  }

  .navbar-nav {
    margin: 0 auto;
    padding: 0;
  }

  .navbar-nav>li {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
  }

  .navbar-nav>li>a {
    width: 10rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
    border-top: none;
    border-radius: 1rem;
  }

  .navbar-nav>li>a:hover {
    border-top: none;
    color: #0077cc !important;
    color: var(--primary-accent) !important;
  }

  .navbar-nav>li>a.active,
  .navbar-nav>li>a.active:focus {
    border-top: none;
    color: #0077cc !important;
    color: var(--primary-accent) !important;
  }

  #home {
    background-color: #3a3a3a;
  }

  #about {
    background-color: #3a3a3a;
  }

  #resume {
    background-color: #3a3a3a;
  }
}

