.project_card {
  background-color: #f8f8f8;
  background-color: var(--primary-white);
  background-size: cover;
  padding: 6rem 0 0;
  margin: 0.75rem;
  /* flex-grow: 1; */
  border-radius: 0.5rem;
  width: 36ch;
  max-width: 36ch;
  height: 20rem;
  overflow: hidden;
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
  color: var(--White);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.project_card:hover,
.project_card:focus-within {
  -webkit-transform: scale(1.05) !important;
          transform: scale(1.05) !important;
}

.card_content {
  --padding: 1.75rem;
  padding: var(--padding);
  background: linear-gradient(hsl(0 0% 0% / 0),
      hsl(0 0% 0% / 0.25),
      hsl(0 0% 0% / 0.50),
      hsl(0 0% 0% / 0.65) 15%,
      hsl(0 0% 0% / 1));
}


.project_card:hover .card_content>*:not(.card_title),
.project_card:focus-within .card_content>*:not(.card_title) {
  opacity: 1;
  /* For when card content will show up */
  transition-delay: 575ms;
}


.card_title {
  position: relative;
  width: -webkit-max-content;
  width: max-content;
  padding-bottom: 0.25rem;
  /* font-family: 'Roboto Slab', serif; */
  font-size: 1.75rem;
  text-transform: uppercase;
  color: #f8f8f8;
  color: var(--primary-white);
}

.card_title::after {
  content: "";
  position: absolute;
  left: calc(var(--padding) * -1);
  bottom: 0;
  height: 4px;
  width: calc(100% + var(--padding));
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  background: #0077cc;
  background: var(--primary-accent);
  /* For orange line on project card hover*/
  transition: -webkit-transform 200ms ease;
  transition: transform 200ms ease;
  transition: transform 200ms ease, -webkit-transform 200ms ease;
  -webkit-transform-origin: left;
          transform-origin: left;

}

.project_card:hover .card_title::after,
.project_card:focus-within .card_title::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.card_body {
  color: rgb(255 255 255 / .95);
  padding-top: 0.25rem;
  padding-bottom: 0.80rem;
  /* font-family: 'Roboto Slab', serif; */
  font-size: 1.25rem;
  text-shadow: 1px 1px black;
}

