/* Square portion for arrows in about section */
.square {
  position: relative;

}

.square::after,
.square::before {
  position: absolute;
  content: '';
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 15rem;
  height: 8rem;
  background: #f8f8f8;
  background: var(--primary-white);
  bottom: -1em;

}

.square::before {
  right: 50%;
  -webkit-transform: skew(0, 20deg);
          transform: skew(0, 20deg);

}

.square::after {
